nav{
    height: 5rem;
    width: 100vw;
    background-color: var(--color-priary);
    display: grid;
    place-items: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
}
.nav__toggle-btn{
    display: none;
}
.nav__container{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
}
.logo{
    width: 7rem;
    display: block;
}
.nav__links{
    display: flex;
    gap: 3.5rem;
    align-items: center;
}
.nav__links a{
    transition: var(--transition);
}
.nav__links a:hover{
    color: var(--color-secondary);

}
.active-nav{
    position: relative;
    transition: var(--transition);
}

.active-nav::after{
    content: '';
    display: block;
    left: calc(50% - 0.6rem);
    transform: rotate(45deg);
    margin-top: 0.9rem;
    position: absolute;
    width: 1.2rem;
    height: 1.2rem;
    background: var(--color-priary);

}

/* MEDIA QUERIES (medium and small screens) */
@media screen and (max-width:1024px) {
    .nav__toggle-btn{
        display: inline-block;
        font-size: 1.8rem;
        cursor: pointer;
        background: transparent;
    }
    .nav__toggle-btn svg{
        color: var(--color-gray-100);
    }
    .nav__links{
        position: absolute;
        top: 100%;
        right: 0;
        flex-direction: column;
        gap: 0;
    }
    .active-nav,.active-nav::after{
        display: none;
    }
    .nav__links li{
        height: 4rem;
        width: 100%;
        box-shadow: -2rem 2rem 5rem rgba(0, 0, 0, 0.4);
        transform: rotateX(90deg);
        opacity: 0;
        transform-origin: top;
        animation: navAnimation 600ms ease forwards;
    }
    .nav__links li:nth-child(2){
        animation-delay: 200ms;
    }
    .nav__links li:nth-child(3){
        animation-delay: 400ms;
    }
    .nav__links li:nth-child(4){
        animation-delay: 600ms;
    }
    .nav__links li:nth-child(5){
        animation-delay: 800ms;
    }
    .nav__links li:nth-child(6){
        animation-delay: 1s;
    }
    @keyframes navAnimation {
        to{
            transform: rotateX(0);
            opacity: 1;
        }
        
    }
    .nav__links li a{
        background-color: var(--color-priary);
        height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        padding: 1rem 5rem 1rem 3rem;
    }
    .show_nav{
        display: flex;
    }
    .hide-nav{
        display: none;
    }
}


























